.Card * {
    color: white;
    cursor: pointer;
}

.Card p {
    font-size: 1.2rem;
}

.Card h2 {
    font-size: 2rem;
    text-align: center;
}

.Card {
    position: relative;
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    width: 230px;
    height: 260px;

    background-color: rgb(18, 18, 18);

    -webkit-box-shadow: 0px 0px 11px 2px rgba(221, 242, 236, 0.68);
    -moz-box-shadow: 0px 0px 11px 2px rgba(130, 150, 144, 0.68);
    box-shadow: 0px 0px 11px 2px rgba(159, 176, 237, 0.68);
}

.Card:hover {
    background-color: rgba(255, 255, 255, 0.186);
}

.Card .background-image {
    position: absolute;
    opacity: 0.1;
    width: 100%;
    height: 100%;
    background-color: rgb(24, 24, 24);
    filter: grayscale(100%);
}

.Card .title-image {
    height: 90%;
    width: auto;
}

.Card div:nth-child(2) {
    z-index: 2;
    position: absolute;
    flex: 1;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 80px;
    width: 80px;
    left: 0%;
    transform: translate(-25%, -25%);

    border-radius: 50%;
    background-color: black;

    border: 2px solid rgba(255, 255, 255, 0.881);

    -webkit-box-shadow: 0px 0px 5px 2px rgba(221, 242, 236, 0.68);
    -moz-box-shadow: 0px 0px 7px 2px rgba(130, 150, 144, 0.68);
    box-shadow: 0px 0px 4px 2px rgba(159, 176, 237, 0.68);
}

.Card div:nth-child(3) {
    z-index: 1;
    flex: 3;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 20px;
    align-items: center;
    border: 2px solid rgb(193, 193, 250);

}

.Card div:nth-child(2) span {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    text-align: center;
    width: 100%;
    height: 100%;
}